import './globals.css';
import Navbar from '@/components/Navbar';

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="th">
      <body className="font-sans antialiased">
        {/* Navbar ต้องอยู่ใน body */}
        <Navbar />
        {children}
      </body>
    </html>
  );
}
